@import 'common/style/variables';
@import 'common/style/mixins';

/*-------  Reset CSS   ----------*/
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    outline: none !important;

}

li {
    list-style: none;
}

a {
    text-decoration: none;
    color: inherit;
}




.App {
    color: #fff;
    font-family: 'Poppins', sans-serif;
    font-size: $fontSizeDesktop;
    min-width: 350px;
    overflow: hidden;
}


